import React, { useContext } from "react";
import ReactMarkdown from "react-markdown";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faArrowRight,
  faBook,
  faMobileAlt,
  faVolumeUp,
} from "@fortawesome/free-solid-svg-icons";
import { faAmazon } from "@fortawesome/free-brands-svg-icons";

import { ThemeContext } from "./../App/App";

import styles from "./LitCard.module.scss";

export default function LitCard({
  title,
  categories,
  description,
  image,
  datePublished,
  publication,
  formats,
  language,
  link,
  GRLink,
  buy,
  featured,
}) {
  const theme = useContext(ThemeContext);
  const classes = [styles["lit-card"], styles[theme]];
  const categoriesOutput = [];
  const formatsOutput = formats.map((format, index) => {
    let iconType;

    switch (format) {
      case "Print":
        iconType = faBook;
        break;
      case "Kindle":
        iconType = faAmazon;
        break;
      case "Audio":
        iconType = faVolumeUp;
        break;
      case "Online":
      default:
        iconType = faMobileAlt;
        break;
    }

    return <FontAwesomeIcon key={index} icon={iconType} title={format} />;
  });

  for (let i = 0; i < categories.length; i++) {
    categoriesOutput.push(" " + categories[i] + " ");

    if (i < categories.length - 1) {
      categoriesOutput.push(<FontAwesomeIcon key={i} icon={faArrowRight} />);
    }
  }

  if (featured) {
    classes.push(styles["featured"]);
  }

  if (datePublished instanceof Date === false) {
    datePublished = new Date(datePublished);
  }

  return (
    <div className={classes.join(" ")}>
      <h4 className={styles["title"]}>
        <div>{title}</div>
      </h4>
      <div className={styles["categories"]}>{categoriesOutput}</div>
      <div className={styles["details"]}>
        <div className={styles["description"]}>
          <ReactMarkdown>{description}</ReactMarkdown>
        </div>
        {image ? (
          <div
            className={styles["with-image"]}
            style={{ backgroundImage: `url(${image})` }}
          />
        ) : (
          <div />
        )}
      </div>
      <p className={styles["publication"]}>
        Published on {datePublished.toLocaleDateString()} in{" "}
        <strong>{publication}</strong>
      </p>
      <p className={styles["formats"]}>
        <strong>Formats:</strong> {formatsOutput}
      </p>
      <p>
        <strong>Language:</strong> {language}
      </p>
      <p className={styles["read-online"]}>
        Read online here:{" "}
        {link ? (
          <a href={link} target="_blank" rel="noreferrer">
            {decodeURIComponent(link)}
          </a>
        ) : (
          "N/A"
        )}
      </p>
      {buy ? (
        <p>
          <ReactMarkdown linkTarget="_blank">{buy}</ReactMarkdown>
        </p>
      ) : null}
      {GRLink ? (
        <a
          href={GRLink}
          target="_blank"
          rel="noreferrer"
          className={styles["good-reads-icon"]}
          title="Goodreads page"
        >
          g
        </a>
      ) : null}
    </div>
  );
}
